import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BroCodeComponent } from './bro-code/bro-code.component';
import { ApidocumentationComponent } from './apidocumentation/apidocumentation.component';

const routes: Routes = [{ path: '', component: BroCodeComponent },
                        { path: 'api', component: ApidocumentationComponent },];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
