import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Injectable } from '@angular/core';
import { BroCode, FormToSend} from '../Models/bro.code.model';
import { Observable } from 'rxjs';
import { HttpHeaders } from '@angular/common/http';  


@Injectable({
    providedIn: 'root'
})
export class BroCodeServices {

    constructor(
        private http: HttpClient
    ) { }
    getRandom() {
        return this.http.get<any>(environment.apiUrl + "Bro/GetRandom");
    }

    // insertForm(FormToSend: FormToSend): Observable<FormToSend> {
    //     return this.http.post<FormToSend>(environment.apiUrl+"ContactForm/PostContactForm", FormToSend)
    //   }


    insertForm(FormTOsendPost: FormToSend): Observable<FormToSend> {  
        const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json'}) };  
        return this.http.post<FormToSend>(  environment.apiUrl + "ContactForm/PostContactForm",FormTOsendPost, httpOptions); 
    }



}